.tab-default {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    border-bottom: 3px solid #F1F8F5;
    a {
        display: block;
        font-size: 16px;
        font-weight: 400;
        color: #656867;
        padding: 6px 16px;
        position: relative;
        .sup {
            background: rgba(255, 79, 39, 1);
            padding: 2px 4px;
            position: absolute;
            right: -8px;
            top: -10px;
            color: #fff;
            border-radius: 4px;
            font-size: 11px;
            font-weight: 600;
        }
        &.active {
            color: #00110B;
            &::before {
                content: "";
                background: var(--colormain);
                height: 3px;
                position: absolute;
                bottom: 0;
                width: 100%;
                left: 0;
            }
        }
        &:hover {
            color: inherit;
        }
    }
    &.line-bottom {
        padding-bottom: 20px;
    }
    &.tab-border {
        a {
            background: #F8FBFF;
            border-radius: 93px;
            line-height: 44px;
            font-size: 16px;
            color: #525F7F;
            padding: 0 20px;
            margin-right: 24px;
            &.active {
                background: #00234B;
                color: #fff;
            }
        }
    }
    @media (max-width: 1025px) {
        a {
            font-size: 14px;
            padding: 6px 10px;
        }
    }
    @media (max-width: 767px) {
        display: block;
        width: 100%;
        overflow-x: scroll;
        overflow-y: hidden;
        -webkit-overflow-scrolling: touch;
        white-space: nowrap;
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
        a {
            display: inline-block;
            .sup {
                display: none;
            }
        }
    }
}

.ic-play {
    border-radius: 6px;
    background: rgba(255, 255, 255, .3);
    width: 56px;
    height: 56px;
    line-height: 56px;
    position: absolute;
    text-align: center;
    color: #fff;
    font-size: 20px;
    left: 50%;
    top: 50%;
    margin: -28px 0 0px -28px;
}

.article-itemt {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 24px;
    align-content: start;
    position: relative;
    .article-thumb {
        width: 100px;
        flex-shrink: 0;
        position: relative;
        overflow: hidden;
        .ic-play {
            width: 36px;
            height: 36px;
            line-height: 36px;
            font-size: 14px;
            margin: -18px 0 0px -18px;
        }
    }
    .article-content {
        display: flex;
        flex-wrap: wrap;
        align-content: start;
        gap: 8px;
        .date {
            color: #004FC2;
            font-size: 14px;
            display: flex;
            align-items: center;
            svg {
                margin-right: 5px;
            }
        }
        .tag {
            color: #0000EE;
            font-size: 12px;
            text-transform: uppercase;
        }
        .title_new {
            font-size: 15px;
            font-weight: 500;
            line-height: 1.5;
            margin-bottom: 0;
            width: 100%;
        }
        .teacher {
            font-size: 12px;
            font-weight: 500;
            color: #656867;
        }
        .des {
            font-size: 16px;
            color: #6A6377;
            line-height: 1.6;
            margin-bottom: 0;
            p {
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
    &.in-title {
        .article-thumb {
            width: 100%;
        }
        .article-content {
            position: absolute;
            padding: 10px;
            bottom: 0;
            width: 100%;
            .title_new {
                font-size: 14px;
                color: #fff;
            }
        }
    }
    &.full {
        gap: 12px;
        .article-thumb {
            width: 100%;
            .ic-play {
                width: 56px;
                height: 56px;
                line-height: 56px;
                font-size: 20px;
                margin: -28px 0 0px -28px;
            }
        }
        .article-content {
            width: 100%;
        }
    }
    &.big {
        .article-content {
            .title_new {
                font-size: 28px;
                @media (max-width: 991px) {
                    font-size: 22px;
                }
            }
        }
    }
    @media (max-width: 991px) {
        .article-thumb {
            width: 140px;
        }
    }
}

