.confir_res {
    padding-left: 30px;
    position: relative;
    cursor: pointer;
    display: block;
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
    }
    .checkmark {
        position: absolute;
        top: 2px;
        left: 0;
        height: 18px;
        width: 18px;
        background: #FFFFFF;
        border: 1px solid #d4d4d5;
        box-sizing: border-box;
        border-radius: 4px;
        &:after {
            content: "";
            position: absolute;
            display: none;
            left: 6px;
            top: 1px;
            width: 5px;
            height: 12px;
            border: solid #222;
            border-width: 0 2px 2px 0;
            transform: rotate(45deg);
        }
    }
    input:checked~.checkmark:after {
        display: block;
    }
    // input:checked~.checkmark {
    //     border: 1px solid #222;
    // }
    input:disabled~.checkmark {
        border: 1px solid #EBEBEB;
    }
    input:disabled~.checkmark:after {
        border: solid #EBEBEB;
        border-width: 0 1px 1px 0;
    }
    &.circle {
        .checkmark {
            border-radius: 50%;
            border: 1px solid #999999;
            &:hover {
                border: 1px solid #999999;
            }
            &:after {
                border: solid #fff;
                border-width: 0 1px 1px 0;
            }
        }
        input:checked~.checkmark {
            background: #222;
            border: 1px solid #222;
        }
    }
}

