        height: 58px;
        padding: 0 20px 0 60px;
        color: #666;
        font-size: 18px;
        width: 100%;
        outline: 0;
        border-radius: 4px;
        background: #F4F6F8;
        box-shadow: 0px 2px 15px 0px rgba(0, 0, 0, 0.06);
        @include placeholder {
            color: #757575;
        }
    }
    .icon_seach_web {
        position: absolute;
        background: none;
        width: 36px;
        height: 36px;
        border-radius: 50%;
        border: 0;
        left: 10px;
        top: 10px;
        color: #222;
        cursor: pointer;
        svg {
            width: 24px;
            height: 24px;
        }
    }
    .btn_reset {
        position: absolute;
        top: 14px;
        right: 4px;
        background: none;
        border: 0;
        width: 30px;
        height: 30px;
        text-align: center;
        font-size: 30px;
        font-family: -webkit-body;
        color: #000;
        line-height: 10px;
        cursor: pointer;
        display: none;
        svg {
            width: 20px;
            height: 20px;
        }
    }
    &.active {
        // .icon_seach_web{
        //     display: none;
        // }
        .suggest-search,
        .btn_reset {
            display: block;
        }
    }
    &.crawl {
        .input_search {
            padding: 0 140px 0 20px;
        }
        .btn-default {
            position: absolute;
            right: 10px;
            top: 5px;
            padding: 10px 20px;
            &::before {
                content: "";
                background: #ccc;
                position: absolute;
                width: 1px;
                height: 28px;
                left: -10px;
            }
        }
    }
    @media (max-width: 991px) {
        .input_search {
            font-size: 16px;
            height: 50px;
            padding: 0 20px 0 40px;
        }
        .icon_seach_web {
            left: 5px;
            top: 5px;
            svg {
                width: 18px;
                height: 18px;
            }
        }
        &.crawl {
            .input_search {
                padding: 0 120px 0 20px;
            }
            .btn-default {
                padding: 6px 15px;
                right: 5px;
            }
        }
    }
}

.table-responsive {
    .blue {
        color: #2D5BD1;
    }
    .table {
        thead th {
            border-top: 0;
            border-bottom: 1px solid #00234B;
            color: #525F7F;
            font-weight: 400;
            text-transform: uppercase;
            font-size: 14px;
        }
        th,
        td {
            padding: 15px .75rem;
            font-size: 16px;
            &:last-child {
                text-align: right;
            }
            &:first-child {
                padding-left: 0;
                text-align: left;
            }
        }
        .content {
            width: 400px;
        }
    }
    @media (max-width: 767px) {
        .table {
            th,
            td {
                white-space: nowrap;
            }
        }
    }
}

.tooltip-new {
    position: relative;
    border-bottom: 0!important;
    color: rgba(178, 179, 241, 1);
    font-size: 13px;
    vertical-align: text-bottom;
    .tooltip-content {
        position: absolute;
        bottom: 50px;
        overflow: hidden;
        opacity: 0;
        z-index: -1;
        left: 50%;
        transform: translate(-50%, 0);
        min-width: 160px;
        background: #FFFFFF;
        border-radius: 10px;
        padding: 8px;
        color: #698096;
        text-align: center;
        box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.1);
        transition: 0.3s all;
        &::before {
            width: 15px;
            height: 15px;
            content: '';
            position: absolute;
            bottom: -8px;
            right: 50%;
            margin-right: -9px;
            background: #fff;
            transform: rotate(225deg);
        }
    }
    &:hover {
        .tooltip-content {
            bottom: 30px;
            overflow: visible;
            opacity: 1;
            z-index: 1
        }
    }
}