.topbar {
    position: relative;
    z-index: 9;
    padding: 16px 0;
    display: flex;
    justify-content: end;
    .logo {
        display: none;
    }
    .my-user {
        display: flex;
        align-items: center;
        column-gap: 24px;
        row-gap: 24px;
    }
    .my-login {
        .ava {
            img {
                width: 38px;
                height: 38px;
                border: 2px solid #F1F8F5;
                border-radius: 50%;
            }
        }
    }
}

.side-panel-scroll {
    height: calc(100vh - 90px);
    padding-right: 30px;
    padding-bottom: 50px;
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-width: thin;
    scrollbar-color: #d0d0d0 #f9f9f9;
    &::-webkit-scrollbar {
        width: 7px;
        height: 7px;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 7px;
        background: #d0d0d0;
    }
    &::-webkit-scrollbar-track {
        background: #f9f9f9;
        border-radius: 7px;
    }
    ul {
        border-bottom: 1px solid #DBE2DF;
        li {
            margin-bottom: 8px;
            padding: 10px 0;
            border-radius: 8px;
            transition: .5s;
            position: relative;
            a {
                font-weight: 500;
                color: #012B1C;
                display: flex;
                align-items: center;
                gap: 12px;
                padding-left: 20px;
                position: relative;
                .ic {
                    width: 36px;
                    flex-shrink: 0;
                }
            }
            .number {
                position: absolute;
                right: 0;
                top: 10px;
                font-size: 12px;
                font-weight: 500;
                width: 36px;
                text-align: center;
                border: 1px solid var(--colormain);
                border-radius: 50%;
                line-height: 28px;
            }
            &.active,
            &:hover {
                background: linear-gradient(276deg, rgba(255, 255, 255, 0.00) 0%, #E3F1EC 90.88%);
                transform: translateY(-4px);
                a {
                    &::before {
                        content: "";
                        position: absolute;
                        background: var(--colormain);
                        width: 6px;
                        height: 100%;
                        left: 0;
                    }
                }
            }
        }
    }
    @media (max-width: 1480px) {
        padding-right: 20px;
    }
}