* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box
}

body {
    background: #F1F8F5;
    margin: 0 auto;
    color: #00110B;
    font-family: var(--font-main);
    font-size: 14px;
    line-height: 1.6;
    font-weight: 400;
}

