.container {
    position: relative;
}

ul,
li {
    list-style-type: none;
}

ul.dotted {
    padding-left: 20px;
    li {
        margin-bottom: 5px;
        list-style-type: disc;
    }
}

ol.dotted {
    padding-left: 20px;
    li {
        margin-bottom: 5px;
        list-style-type: inherit;
    }
}

.icon-svg {
    width: 16px;
    height: 16px;
    fill: currentColor;
    vertical-align: middle;
}

img {
    border: 0px;
    font-size: 0;
    line-height: 0;
    max-width: 100%;
    vertical-align: middle;
}

a {
    color: inherit;
    &:hover {
        text-decoration: none;
        color: var(--colormain)
    }
}

p {
    &:last-child {
        margin-bottom: 0;
    }
}

.hidden {
    display: none !important
}

.clearfix {
    clear: both;
    &::after {
        content: "";
        display: table;
    }
}

u {
    font-weight: 400;
}

.uppercase {
    text-transform: uppercase;
}

.regular {
    font-weight: 400;
}

.medium {
    font-weight: 500
}

.bold {
    font-weight: bold
}

