    font-size: 24px!important;
    @media (max-width: 991px) {
        font-size: 18px!important;
    }
}

.txt_28 {
    font-size: 28px;
    @media (max-width: 1199px) {
        font-size: 24px;
    }
    @media (max-width: 991px) {
        font-size: 22px;
    }
}

.txt_30 {
    font-size: 30px;
    @media (max-width: 1199px) {
        font-size: 26px;
    }
    @media (max-width: 991px) {
        font-size: 20px;
    }
}

.txt_48 {
    font-size: 48px;
    @media (max-width: 1199px) {
