.banner-ads{
    float: none;
    font-size: 0;
    text-align: center;
    &.line{
        &>div{
            &>div{
                border-bottom: 1px solid #e5e5e5;
                padding-bottom: 10px;
                margin-bottom: 20px;
            }
        }
    }
}

@media (max-width: 991px) {
    .nav-scroll {
        overflow-x: scroll;
        overflow-y: hidden;
        -webkit-overflow-scrolling: touch;
        white-space: nowrap;
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
    }
}

